import { Storage } from '@ionic/storage';
import { HttpClient } from '@angular/common/http';
import { Component } from '@angular/core';
import { NavController, LoadingController } from 'ionic-angular';


@Component({
  selector: 'page-support',
  templateUrl: 'support.html',
})
export class SupportPage {
  email: string;
  name: string;
  phone: string;
  desc: string;
  
  constructor(public navCtrl: NavController, public loadingCtrl: LoadingController, public http: HttpClient, private storage: Storage) {
    this.storage.get('user').then(user => {
      if (user !== null) {
        this.email = user.email;
        this.name = user.name;
        this.phone = user.phone || '';
      };
    })
  }

  onSubmit() {
    let load = this.loadingCtrl.create({
      content: 'Submitting form...',
      spinner: 'dots'
    });
    load.present();

    this.http.post('https://realm-glory.herokuapp.com/api/support', {
      email: this.email,
      name: this.name,
      phone: this.phone || '',
      message: this.desc
    }).subscribe(status => {
      load.dismiss();
      console.log(status);
      if (status['status'] === 'success') {
        alert('A member of the team would be in touch to help out, thanks.');
        this.navCtrl.pop();
      } else {
        alert('An error occurred, please try again.')
      }
    }, (err) => {
      load.dismiss();
      console.log('err', err)
    });
  }

}



